$(document).ready(function() {

    // Hover-Click
    $(".item").hover(onIn, onOut);
    $(".item .show").click(function() {
        if ($(this).parents(".item").hasClass("item--active")) {
            $(this).parents(".item").removeClass("item--active")
        } else {
            $(this).parents(".item").addClass("item--active")
        }
    })

    // Delete
    $('.delete').click(function () {
        $(this).parents('.delete-item').addClass('delete-item--active')
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".content-inner").find(".tabs__content").removeClass("tabs__content--active")
        $(this).parents(".content-inner").find(`.tabs__content[data-content-path="${path}"]`).addClass("tabs__content--active")
    })

    // Account
    $('.account__info-btn').click(function () {
        $('.account__info').addClass('account__info--active')
        $('.account__form-none').addClass('account__form-none--active')
    })

    $('.cancel-btn').click(function () {
        $('.account__info').removeClass('account__info--active')
        $('.account__form-none').removeClass('account__form-none--active')
    })

    // Select
    $('.domains-main__select-show').click(function () {
        $(this).siblings('.domains-main__select-hide').toggleClass('domains-main__select-hide--active')
    })

    let show = $('.domains-main__select-show')
    let item = $('.domains-main__select-hide')

    $(document).mouseup(function (e) {
        if ( !show.is(e.target) && show.has(e.target).length === 0 &&
            !item.is(e.target) && item.has(e.target).length === 0

        ) {
            item.removeClass('domains-main__select-hide--active')
        }
    })

    // Range
    $("#demo").ionRangeSlider({
        min: 0,
        max: 10,
    })

    //Calendar
    new AirDatepicker('.datepicker-here', {
        inline: true,
        range: true,
        multipleDatesSeparator: " - ",
    })



})

function onIn() {
    if (window.innerWidth > 320) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 320) {
        $(this).removeClass("item--active")
    }
}